import React, { useState, useEffect } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useNavigate } from 'react-router-dom';
import { useInspections } from '../contexts/InspectionsContext';
import { 
  Car, 
  Building, 
  Home, 
  Plus, 
  Trash2, 
  Upload,
  MapPin,
  User,
  Phone,
  Mail,
  FileText,
  AlertCircle
} from 'lucide-react';
import { inspectionsApi } from '../services/api';
import AddressAutocomplete from '../components/AddressAutocomplete';
import GoogleMap from '../components/GoogleMap';
import { getCharacteristicsForPropertyType } from '../data/objectCharacteristics';
import toast from 'react-hot-toast';
import './CreateInspection.css';

const schema = yup.object({
  propertyType: yup.string().required('  '),
  address: yup.string().required(' '),
  latitude: yup.number().optional(),
  longitude: yup.number().optional(),
  inspectorName: yup.string().required('  '),
  inspectorPhone: yup.string().matches(/^\+7\d{10}$/, '  ').required(' '),
  inspectorEmail: yup.string().email('  email').optional(),
  internalNumber: yup.string().optional(),
  comment: yup.string().optional(),
  objects: yup.array().of(
    yup.object().shape({})
  ).min(1, '    '),
});


const propertyTypes = [
  { id: '', name: '', icon: Car, color: '#1976D2' },
  { id: '', name: '', icon: Building, color: '#388E3C' },
  { id: '', name: '', icon: Home, color: '#F57C00' },
];

const CreateInspection: React.FC = () => {
  const [step, setStep] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const [validationErrors, setValidationErrors] = useState<string[]>([]);
  const navigate = useNavigate();
  const { addNewInspection } = useInspections();

  const { register, control, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
    mode: 'onChange', //    
    defaultValues: {
      objects: [{ category: '', type: '', make: '', model: '', vin: '', registrationNumber: '' }]
    }
  });

  const { fields, append, remove } = useFieldArray({
    control,
    name: 'objects'
  });

  const selectedPropertyType = watch('propertyType');
  const formValues = watch();

  //   
  useEffect(() => {
    const errors: string[] = [];
    
    if (!formValues.address) {
      errors.push('  ');
    }
    if (!formValues.latitude || !formValues.longitude) {
      errors.push('  .    ');
    }
    if (!formValues.inspectorName) {
      errors.push('   ');
    }
    if (!formValues.inspectorPhone) {
      errors.push('   ');
    }
    if (!formValues.objects || formValues.objects.length === 0) {
      errors.push('     ');
    } else {
      //      
      const characteristics = getCharacteristicsForPropertyType(formValues.propertyType || '');
      
      formValues.objects.forEach((obj: any, index: number) => {
        //    
        characteristics.forEach((char) => {
          if (char.required && !obj[char.id]) {
            errors.push(` ${index + 1}:   ${char.name.toLowerCase()}`);
          }
        });
      });
    }
    
    setValidationErrors(errors);
  }, [formValues]);

  const onSubmit = async (data: any) => {
    console.log('  :', data);
    setIsLoading(true);
    setValidationErrors([]); //   
    
    //   
    if (!data.propertyType) {
      toast.error('  ');
      setIsLoading(false);
      return;
    }
    
    if (!data.address) {
      toast.error('  ');
      setIsLoading(false);
      return;
    }
    
    if (!data.inspectorName) {
      toast.error('  ');
      setIsLoading(false);
      return;
    }
    
    if (!data.inspectorPhone) {
      toast.error('  ');
      setIsLoading(false);
      return;
    }
    
    if (!data.objects || data.objects.length === 0) {
      toast.error('      ');
      setIsLoading(false);
      return;
    }
    
    try {
      console.log('    ...');
      const response = await inspectionsApi.createInspection(data);
      console.log('  :', response);
      
      const inspectionNumber = response.data?.inspection?.internal_number || `INS-${Date.now()}`;
      
      toast.success(` ${inspectionNumber}     `, {
        duration: 3000
      });
      
      //   
      setIsLoading(false);
      
      //        " "
      addNewInspection({ id: inspectionNumber, status: ' ', ...data });
      
      //         
      setTimeout(() => {
        navigate('/inspections', { 
          replace: true,
          state: { refresh: true, newInspection: inspectionNumber }
        });
      }, 100);
    } catch (error: any) {
      console.error('  :', error);
      
      //    
      let errorMessage = '  ';
      let shouldNavigate = false;
      
      if (error.response) {
        if (error.response.status === 404) {
          errorMessage = ' .    -';
          shouldNavigate = true;
        } else if (error.response.status === 400) {
          errorMessage = ' .   ';
          //    
          if (error.response.data?.errors) {
            const validationErrors = error.response.data.errors.map((err: any) => err.msg).join(', ');
            errorMessage = ` : ${validationErrors}`;
          }
        } else {
          errorMessage = error.response.data?.message || ` : ${error.response.status}`;
        }
      } else if (error.request) {
        errorMessage = ' .    -';
        shouldNavigate = true;
      } else {
        errorMessage = error.message || ' ';
      }
      
      if (shouldNavigate) {
        //        -   " "
        const demoInspectionNumber = 'DEMO-' + Date.now();
        addNewInspection({ id: demoInspectionNumber, status: ' ', ...data });
        
        //  -     
        setTimeout(() => {
          navigate('/inspections', { 
            replace: true,
            state: { refresh: true, newInspection: demoInspectionNumber }
          });
        }, 100);
      } else {
        toast.error(errorMessage);
        setIsLoading(false);
      }
    }
  };

  const handleImportFromExcel = async () => {
    try {
      //     
      const text = await navigator.clipboard.readText();
      
      if (!text) {
        toast.error('  ');
        return;
      }

      //    Excel ( )
      const rows = text.trim().split('\n');
      const importedObjects = rows.map(row => {
        const cols = row.split('\t');
        return {
          vin: cols[0]?.trim() || '',
          registrationNumber: cols[1]?.trim() || '',
          category: cols[2]?.trim() || '',
          type: cols[3]?.trim() || '',
          make: cols[4]?.trim() || '',
          model: cols[5]?.trim() || ''
        };
      }).filter(obj => obj.make || obj.model); //   

      if (importedObjects.length === 0) {
        toast.error('   .    Excel');
        return;
      }

      if (importedObjects.length > 150) {
        toast.error('   - 150');
        return;
      }

      //    
      setValue('objects', importedObjects);
      toast.success(` : ${importedObjects.length}`);
    } catch (error) {
      toast.error(' . ,      ');
    }
  };


  return (
    <div className="create-inspection-page">
      <div className="create-inspection-content">
        <div className="page-header">
          <h1 className="page-title"> </h1>
          <div className="header-actions">
            <button 
              className="btn btn-secondary"
              onClick={() => {
                if (window.confirm(' ,     ?    .')) {
                  navigate('/inspections');
                }
              }}
              type="button"
            >
              
            </button>
            <button 
              className="btn btn-close"
              onClick={() => {
                if (window.confirm(' ,    ?    .')) {
                  navigate('/inspections');
                }
              }}
              title=""
              type="button"
            >
              
            </button>
          </div>
        </div>

      <form onSubmit={handleSubmit(onSubmit)} className="inspection-form">
        {/*  1:    */}
        {step === 1 && (
          <div className="step-content">
            <div className="step-header">
              <h2>  </h2>
              <p>    </p>
            </div>

            <div className="property-types">
              {propertyTypes.map((type) => {
                const IconComponent = type.icon;
                return (
                  <button
                    key={type.id}
                    type="button"
                    className={`property-type-card ${selectedPropertyType === type.id ? 'selected' : ''}`}
                    onClick={() => {
                      setValue('propertyType', type.id);
                      //     
                      setTimeout(() => setStep(2), 300);
                    }}
                  >
                    <div className="property-type-icon" style={{ backgroundColor: type.color }}>
                      <IconComponent size={32} />
                    </div>
                    <span className="property-type-name">{type.name}</span>
                  </button>
                );
              })}
            </div>

            {errors.propertyType && (
              <div className="form-error">{errors.propertyType.message}</div>
            )}
          </div>
        )}

        {/*  2:   */}
        {step === 2 && (
          <div className="step-content">
            <div className="step-header">
              <h2>  </h2>
              <p>    </p>
            </div>

            <div className="form-grid">
              <div className="form-group address-with-map">
                <div className="address-input">
                  <label className="form-label required">
                    <MapPin size={16} />
                    
                  </label>
                  <AddressAutocomplete
                    value={watch('address') || ''}
                    onChange={(address, lat, lon) => {
                      setValue('address', address);
                      if (lat && lon) {
                        setValue('latitude', lat);
                        setValue('longitude', lon);
                        toast.success(' ');
                      }
                    }}
                    placeholder="      "
                    error={errors.address?.message}
                  />
                </div>
                <div className="map-container">
                  <label className="form-label">
                    <MapPin size={16} />
                    
                  </label>
                  <GoogleMap
                    latitude={watch('latitude')}
                    longitude={watch('longitude')}
                    address={watch('address')}
                    onLocationChange={(lat, lon, address) => {
                      setValue('latitude', lat);
                      setValue('longitude', lon);
                      setValue('address', address);
                      toast.success(' ');
                    }}
                    height="250px"
                  />
                </div>
              </div>

              <div className="form-group">
                <label className="form-label">
                  <FileText size={16} />
                   
                </label>
                <input
                  type="text"
                  className="form-input"
                  {...register('internalNumber')}
                  placeholder="  "
                />
              </div>

              <div className="form-group">
                <label className="form-label required">
                  <User size={16} />
                   
                </label>
                <input
                  type="text"
                  className="form-input"
                  {...register('inspectorName')}
                  placeholder="  "
                />
                {errors.inspectorName && (
                  <div className="form-error">{errors.inspectorName.message}</div>
                )}
              </div>

              <div className="form-group">
                <label className="form-label required">
                  <Phone size={16} />
                   
                </label>
                <input
                  type="tel"
                  className="form-input"
                  {...register('inspectorPhone')}
                  placeholder="+7 (999) 123-45-67"
                />
                {errors.inspectorPhone && (
                  <div className="form-error">{errors.inspectorPhone.message}</div>
                )}
              </div>

              <div className="form-group">
                <label className="form-label">
                  <Mail size={16} />
                  Email 
                </label>
                <input
                  type="email"
                  className="form-input"
                  {...register('inspectorEmail')}
                  placeholder="email@example.com"
                />
                {errors.inspectorEmail && (
                  <div className="form-error">{errors.inspectorEmail.message}</div>
                )}
              </div>

              <div className="form-group full-width">
                <label className="form-label">
                  
                </label>
                <textarea
                  className="form-input"
                  rows={3}
                  {...register('comment')}
                  placeholder="   "
                />
              </div>
            </div>

            <div className="step-actions">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => setStep(1)}
              >
                
              </button>
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => setStep(3)}
              >
                
              </button>
            </div>
          </div>
        )}

        {/*  3:   */}
        {step === 3 && (
          <div className="step-content">
            <div className="step-header">
              <h2> </h2>
              <p>   </p>
            </div>

            <div className="objects-section">
              <div className="objects-header">
                <h3> </h3>
                <div className="objects-actions">
                  <button
                    type="button"
                    className="btn btn-secondary btn-sm"
                    onClick={handleImportFromExcel}
                  >
                    <Upload size={16} />
                      Excel
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary btn-sm"
                    onClick={() => {
                      if (fields.length >= 150) {
                        toast.error('   - 150');
                        return;
                      }
                      append({ category: '', type: '', make: '', model: '', vin: '', registrationNumber: '' });
                    }}
                    disabled={fields.length >= 150}
                  >
                    <Plus size={16} />
                      ({fields.length}/150)
                  </button>
                </div>
              </div>

              <div className="objects-list">
                {fields.map((field, index) => {
                  const characteristics = getCharacteristicsForPropertyType(selectedPropertyType || '');
                  
                  return (
                    <div key={field.id} className="object-card">
                      <div className="object-header">
                        <h4> #{index + 1}</h4>
                        <button
                          type="button"
                          className="btn btn-error btn-sm"
                          onClick={() => remove(index)}
                          disabled={fields.length === 1}
                        >
                          <Trash2 size={14} />
                        </button>
                      </div>
                      
                      <div className="object-characteristics">
                        {characteristics.map((char) => (
                          <div key={char.id} className="form-group">
                            <label className="form-label">
                              {char.name}
                              {char.required && <span className="required">*</span>}
                            </label>
                            
                            {char.type === 'select' ? (
                              <select
                                className={`form-select ${(errors.objects as any)?.[index]?.[char.id] ? 'input-error' : ''}`}
                                {...register(`objects.${index}.${char.id}` as any)}
                              >
                                <option value=""> {char.name.toLowerCase()}</option>
                                {char.options?.map(option => (
                                  <option key={option} value={option}>{option}</option>
                                ))}
                              </select>
                            ) : (
                              <input
                                type={char.type}
                                className={`form-input ${(errors.objects as any)?.[index]?.[char.id] ? 'input-error' : ''}`}
                                {...register(`objects.${index}.${char.id}` as any)}
                                placeholder={char.placeholder}
                              />
                            )}
                            
                            {(errors.objects as any)?.[index]?.[char.id] && (
                              <div className="form-error-inline">{(errors.objects as any)[index]?.[char.id]?.message}</div>
                            )}
                          </div>
                        ))}
                      </div>
                    </div>
                  );
                })}
              </div>

              {errors.objects && (
                <div className="form-error">{errors.objects.message}</div>
              )}
            </div>

            <div className="step-actions">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => setStep(1)}
              >
                
              </button>
              <div className="submit-button-wrapper">
                <button
                  type="submit"
                  className={`btn btn-primary ${validationErrors.length > 0 ? 'btn-disabled' : ''}`}
                  disabled={isLoading || validationErrors.length > 0}
                  title={validationErrors.length > 0 ? validationErrors.join('\n') : ' '}
                >
                  {isLoading ? '...' : ''}
                </button>
                {validationErrors.length > 0 && (
                  <div className="validation-tooltip">
                    <AlertCircle size={16} />
                    <div className="tooltip-content">
                      <strong> :</strong>
                      <ul>
                        {validationErrors.map((error, index) => (
                          <li key={index}>{error}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </form>
      </div>
    </div>
  );
};

export default CreateInspection;
