import React from 'react';
import { Outlet, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';
import { useInspections } from '../contexts/InspectionsContext';
import { 
  FileText, 
  Plus, 
  LogOut, 
  X,
  BarChart3,
  Users,
  Settings,
  ChevronLeft,
  ChevronRight,
  Palette,
  Sun,
  Moon,
  Monitor,
  Zap,
  Code
} from 'lucide-react';
import './Layout.css';

const Layout: React.FC = () => {
  const { user, logout } = useAuth();
  const { theme, setTheme } = useTheme();
  const { inspectionsCount } = useInspections();
  const navigate = useNavigate();
  const location = useLocation();
  const [sidebarOpen, setSidebarOpen] = React.useState(false);
  const [sidebarCollapsed, setSidebarCollapsed] = React.useState(false);
  const [showThemeSelector, setShowThemeSelector] = React.useState(false);

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const isActive = (path: string) => location.pathname === path;

  return (
    <div className="layout">
      {/*   -  */}

      {/*   */}
      <aside className={`sidebar ${sidebarOpen ? 'open' : ''} ${sidebarCollapsed ? 'collapsed' : ''}`}>
        <div className="sidebar-header">
          <div className="sidebar-logo">
            <div className="logo-icon">
              <div className="logo-shield"></div>
            </div>
            {!sidebarCollapsed && <span className="logo-text">CMS Check</span>}
          </div>
          <button 
            className="sidebar-close"
            onClick={() => setSidebarOpen(false)}
          >
            <X size={20} />
          </button>
        </div>

        <nav className="sidebar-nav">
          <button
            className={`nav-item ${isActive('/inspections') ? 'active' : ''}`}
            onClick={() => {
              navigate('/inspections');
              setSidebarOpen(false);
            }}
          >
            <FileText size={20} />
            <span></span>
            <span className="badge">{inspectionsCount}</span>
          </button>
          
          <button
            className={`nav-item ${isActive('/inspections/create') ? 'active' : ''}`}
            onClick={() => {
              navigate('/inspections/create');
              setSidebarOpen(false);
            }}
          >
            <Plus size={20} />
            <span> </span>
          </button>

          <button
            className={`nav-item ${isActive('/analytics') ? 'active' : ''}`}
            onClick={() => {
              navigate('/analytics');
              setSidebarOpen(false);
            }}
          >
            <BarChart3 size={20} />
            <span></span>
          </button>

          <button
            className={`nav-item ${isActive('/users') ? 'active' : ''}`}
            onClick={() => {
              navigate('/users');
              setSidebarOpen(false);
            }}
          >
            <Users size={20} />
            <span></span>
          </button>

          <button
            className={`nav-item ${isActive('/settings') ? 'active' : ''}`}
            onClick={() => {
              navigate('/settings');
              setSidebarOpen(false);
            }}
          >
            <Settings size={20} />
            <span></span>
          </button>
        </nav>

        <div className="sidebar-footer">
          {/*   */}
          <div className="theme-selector">
            <button 
              className="theme-toggle-btn"
              onClick={() => setShowThemeSelector(!showThemeSelector)}
            >
              <Palette size={16} />
              {!sidebarCollapsed && <span></span>}
            </button>
            
            {showThemeSelector && (
              <div className="theme-options">
                <button 
                  className={`theme-option ${theme === 'light' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('light');
                    setShowThemeSelector(false);
                  }}
                >
                  <Sun size={14} />
                  {!sidebarCollapsed && <span></span>}
                </button>
                <button 
                  className={`theme-option ${theme === 'dark' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('dark');
                    setShowThemeSelector(false);
                  }}
                >
                  <Moon size={14} />
                  {!sidebarCollapsed && <span></span>}
                </button>
                <button 
                  className={`theme-option ${theme === 'gray' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('gray');
                    setShowThemeSelector(false);
                  }}
                >
                  <Monitor size={14} />
                  {!sidebarCollapsed && <span></span>}
                </button>
                <button 
                  className={`theme-option ${theme === 'monochrome' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('monochrome');
                    setShowThemeSelector(false);
                  }}
                >
                  <Zap size={14} />
                  {!sidebarCollapsed && <span></span>}
                </button>
                <button 
                  className={`theme-option ${theme === 'windows97' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('windows97');
                    setShowThemeSelector(false);
                  }}
                >
                  <Monitor size={14} />
                  {!sidebarCollapsed && <span>Windows 97</span>}
                </button>
                <button 
                  className={`theme-option ${theme === 'matrix' ? 'active' : ''}`}
                  onClick={() => {
                    setTheme('matrix');
                    setShowThemeSelector(false);
                  }}
                >
                  <Code size={14} />
                  {!sidebarCollapsed && <span></span>}
                </button>
              </div>
            )}
          </div>

          {/*   */}
          <button 
            className="collapse-btn green-pulse"
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
            title={sidebarCollapsed ? '' : ''}
          >
            {sidebarCollapsed ? <ChevronRight size={16} /> : <ChevronLeft size={16} />}
            {!sidebarCollapsed && <span></span>}
          </button>

          <div className="user-info">
            <div className="user-avatar">
              {user?.fullName?.charAt(0) || 'U'}
            </div>
            {!sidebarCollapsed && (
              <div className="user-details">
                <div className="user-name">{user?.fullName}</div>
                <div className="user-role">{user?.role}</div>
              </div>
            )}
          </div>
          <button className="logout-btn" onClick={handleLogout}>
            <LogOut size={16} />
            {!sidebarCollapsed && <span></span>}
          </button>
        </div>
      </aside>

      {/*   */}
      <main className="main-content">
        <div className="content-body">
          <Outlet />
        </div>
      </main>

      {/*     */}
      {sidebarOpen && (
        <div 
          className="sidebar-overlay"
          onClick={() => setSidebarOpen(false)}
        />
      )}
    </div>
  );
};

export default Layout;
